<template lang="html">
  <div class="">
    <jwdheaders></jwdheaders>
    <el-row style="width:1200px;margin:0 auto;">
      <el-col :span="4"><div class="grid-content bg-purple">
        <jwdzly></jwdzly>
      </div></el-col>
      <el-col :span="20"><div class="grid-content bg-purple">
        <!--内容-->
            <div class="container">
               <div class="txhead">
               <span>绑定提现银行卡</span>
              </div>
              <p class="becareful">
               信用卡、国际卡、部分存折卡不能提现
              </p>
              <!--表单验证-->
            <el-form :model="ruleForm"  ref="ruleForm" label-width="150px" class="demo-ruleForm">
              <el-form-item label="银行卡类型：" :rules="{required: true, trigger: 'blur'}">
                    <span>储蓄卡（借记卡）</span>
              </el-form-item>
              <el-form-item label="真实开户人姓名" :rules="{required: true, trigger: 'blur'}">
                    <input type="text" class="kun">
              </el-form-item>
              <el-form-item label="选择银行卡：" :rules="{required: true, trigger: 'blur'}">
                    <el-select v-model="formInline.region" placeholder="银行">
                    <el-option label="邮政银行" value="shanghai"></el-option>
                    <el-option label="农业银行" value="beijing"></el-option>
                    </el-select>
               </el-form-item>
               <el-form-item label="选择地区：" :rules="{required: true, trigger: 'blur'}">
                    <el-select v-model="formInline1.region" placeholder="省">
                    <el-option label="江西省" value="shanghai"></el-option>
                    <el-option label="山西省" value="beijing"></el-option>
                    </el-select>
                    <el-select v-model="formInline2.region" placeholder="县">
                    <el-option label="阳城县" value="shanghai"></el-option>
                    <el-option label="河北县" value="beijing"></el-option>
                    </el-select>
               </el-form-item>
               <el-form-item label="银行卡号：" :rules="{required: true, trigger: 'blur'}">
                    <el-input></el-input>
               </el-form-item>

            </el-form><!--表单验证结束-->
                  <p class="money-info">
                 此银行卡的开户名必须为真实的，否则提现会失败！
                </p>
                <router-link to="/jwdtixian3" class="el-button next" tag="button">
                 保存账号</router-link>
            </div> <!--内容结束-->
      </div></el-col>
    </el-row>


  </div>
</template>

<script>
import jwdzly from "@/components/jwdzly"
import jwdheaders from "@/components/jwdheads"
export default {
  components:{
    jwdheaders,
    jwdzly
  },
  data(){
    return {
            formInline: {
            user: '',
            region: ''
            },
            formInline1: {
            user: '',
            region: ''
            },
            formInline2: {
            user: '',
            region: ''
            }
        }
  }


}
</script>

<style lang="less" scoped>
.header-wrapper{
    width: 1200px;

    margin: 0 auto;
    border-bottom:1px solid grey;
    &:after{
      content: '';
      display: block;
      clear: both;
    }
    img{
      float: left;
      padding: 7px 0;
    }
    ul{
      float: left;
      &:after{
        content: '';
        display: block;
        clear: both;
      }
      li{
        float: left;
        font-size: 18px;
        color: #343434;
        margin: 0 25px;
        line-height: 80px;
        &.act{
          color: #2e51a5;
          font-weight: bolder;
          border-bottom: 2px solid #2e51a5;
        }
      }
    }

  }
   .container{
      font-text:12px;
      width:790px;
      margin:20px;
      padding:20px;
      border:1px solid black;
     .txhead{
         border-bottom:1px solid #d0d0d0;
         padding-bottom:10px;
         span{
             font-text:16px;
             font-weight:bolder;

         }
     }
     .becareful{
         text-align:center;
          color:#7d726e;
          margin:50px 0 26px;
     }
     .el-form {
         .kun{
             width:80px;
             height:26px;
             background:#4cacc6;
             border:3px solid #28768a;
             border-radius:10px;
         }
         .el-input{
             width:220px;
         }
     }
     .money-info{
             margin-left:200px;
             margin-top:24px;
             color:#babbba;
         }
       .next{
         margin:24px 0 24px 200px;

             background:red;
             color:white;
     }
 }
</style>



